var data = [
    {
        number: 2759404,
        name: 'Платье',
        image: 'data:image/png;base64,',
        newPrice: 15.99,
        oldPrice: 20.99
    },
    {
        number: 2742123,
        name: 'Платье',
        image: 'data:image/png;base64,',
        newPrice: 20.99,
        oldPrice: 25.99
    },
    {
        number: 2736558,
        name: 'Платье',
        image: 'data:image/png;base64,',
        newPrice: 25.99,
        oldPrice: 30.99
    }
    ]

var template = document.querySelector('#lodash')
for (var i = 0; i < data.length; i++) {
    var elem = data[i]

    var clone = template.content.cloneNode(true)
    var cells_0 = clone.querySelectorAll('img')
    var cells_1 = clone.querySelectorAll('h3')
    var cells_2 = clone.querySelectorAll('p')
    var cells_3 = clone.querySelectorAll('.price')
    var cells_4 = clone.querySelectorAll('span')

    cells_0[0].src = elem.image
    cells_1[0].textContent = 'Дизайн #' + elem.number
    cells_2[0].textContent = elem.name
    cells_3[0].textContent = '$' + elem.newPrice
    cells_4[0].textContent = '$' + elem.oldPrice

    template.parentNode.appendChild(clone)
}